import React, { useCallback } from "react";
import { connect } from "react-redux";
import {
  setFilterSettings,
  setFilterStatus,
  setStatus,
} from "../../store/reducers/filter";
import styles from "./filter.module.scss";

const Filter = ({
  onDeActivateFilter,
  isFiltered,
  setFilterSettings,
  setFilterStatus,
  allProductInformation,
}) => {
  const onCategoryItemClick = useCallback((e) => {
    setFilterSettings("category", e.target.textContent);
    setFilterStatus(true);
  }, []);
  const onSizeItemClick = useCallback((e) => {
    setFilterSettings("size", e.target.textContent);
    setFilterStatus(true);
  }, []);
  const onColorsItemClick = useCallback((e) => {
    setFilterSettings("colors", e.target.textContent);
    setFilterStatus(true);
  }, []);
  const onBrandsItemClick = useCallback((e) => {
    setFilterSettings("brands", e.target.textContent);
    setFilterStatus(true);
  }, []);
  return (
    <div className={styles.filter}>
      <div className={styles.filterWrapper}>
        <div className={styles.filterCategory}>
          <h4>Category</h4>
          <ul>
            {allProductInformation.category.map((item) => (
              <li onClick={onCategoryItemClick} key={item}>
                {item}
              </li>
            ))}
          </ul>
        </div>
        <div className={styles.filterCharacteristics}>
          <div className={styles.filterSize}>
            <h4>Size</h4>
            <div className={styles.filterSizes}>
              {allProductInformation.size.map((item) => (
                <span onClick={onSizeItemClick} key={item}>
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className={styles.filterColors}>
            <h4>Colors</h4>
            <div className={styles.filterColorsItem}>
              {allProductInformation.colors.map((item) => (
                <div
                  onClick={onColorsItemClick}
                  key={item}
                  className={styles[`${item}`]}
                ></div>
              ))}
            </div>
          </div>
          <div className={styles.filterPriceRange}>
            <h4>Price Range</h4>
            <div class={styles.priceSlider}>
              <span>
                from
                <input type="number" value="0" min="0" max="120000" /> to
                <input type="number" value="120000" min="0" max="120000" />
              </span>
              <input
                value="25000"
                min="0"
                max="120000"
                step="500"
                type="range"
              />
              <input
                value="50000"
                min="0"
                max="120000"
                step="500"
                type="range"
              />
              <svg width="100%" height="24">
                <line
                  x1="4"
                  y1="0"
                  x2="300"
                  y2="0"
                  stroke="#212121"
                  stroke-width="12"
                  stroke-dasharray="1 28"
                ></line>
              </svg>
            </div>
          </div>
        </div>
        <div className={styles.filterBrands}>
          <h4>Brands</h4>
          <div className={styles.filterBrandsItems}>
            <ul>
              {allProductInformation.brands.map((item) => (
                <li onClick={onBrandsItemClick} key={item}>
                  {item}
                </li>
              ))}
            </ul>
          </div>
        </div>
        <button onClick={onDeActivateFilter} className={styles.filterApply} />
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isFiltered: state.filter.isFiltered,
  allProductInformation: state.allProductInformation,
});
export default connect(mapStateToProps, { setFilterStatus, setFilterSettings })(
  Filter
);
