const TOGGLE_CART = "TOGGLE_CART";
const ADD_PRODUCT = "ADD_PRODUCT";
const ADD_SELECTED_SIZE = "ADD_SELECTED_SIZE";

let initialState = {
  isCart: false,
  cart: [],
  size: [],
  cartId: [],
};
//!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
let a = 0;
export const cart = (state = initialState, action) => {
  switch (action.type) {
    case TOGGLE_CART: {
      return {
        ...state,
        isCart: action.status,
      };
    }
    case ADD_PRODUCT: {
      return {
        ...state,
        cart: [...state.cart, action.product],
        //!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
        cartId: [...state.cartId, a++],
      };
    }
    case ADD_SELECTED_SIZE: {
      return {
        ...state,
        size: [...state.size, action.size],
      };
    }
    default: {
      return {
        ...state,
      };
    }
  }
};

//action creator
export const getStatus = (status) => {
  return {
    type: TOGGLE_CART,
    status,
  };
};
export const addProduct = (product) => {
  return {
    type: ADD_PRODUCT,
    product,
  };
};
export const addSelectedSize = (size) => {
  return {
    type: ADD_SELECTED_SIZE,
    size,
  };
};

