import React, { useEffect } from "react";
import styles from "./catalog.module.scss";

import productsStyle from "../common/ProductItem/productItem.module.scss";
import ProductItem from "../common/ProductItem/ProductItem";
import { connect } from "react-redux";
import { getGoods } from "../../store/reducers/goods";

const CatalogFootWear = (props) => {
  if (props.goods === null) return <div>none</div>;

  return (
    <div className={styles.catalog}>
      <div className={styles.searchInput}>
        <input value="search" />
      </div>

      <p className={styles.catalogTitle}>Footwear</p>

      <div className={productsStyle.product}>
        <div className={productsStyle.productItems}>
          {props.filter.isFiltered
            ? props.goods.map((item) =>
                props.filter.brands.map((elem) => {
                  if (item.brand === elem) {
                    return <ProductItem key={item.id} good={item} />;
                  }
                })
              )
            : props.goods.map((item) => (
                <ProductItem key={item.id} good={item} />
              ))}
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({
  goods: state.goods.goods,
  filter: state.filter,
});

export default connect(mapStateToProps, getGoods)(CatalogFootWear);
