import React from "react";
import styles from "./characteristic.module.scss";
import { Formik, Form, Field } from "formik";
import { addToCartCurrentProduct } from "../../common/addToCartCurrentProduct/addToCartCurrentProduct";
import { connect, Provider } from "react-redux";
import { addProduct, addSelectedSize } from "../../../store/reducers/cart";

const Characteristic = ({ product, addProduct, addSelectedSize }) => {
  const addToCart = (values) => {
    addToCartCurrentProduct(values.size, product, addProduct, addSelectedSize);
  };
  return (
    <div className={styles.characteristic}>
      <div className={styles.characteristicContent}>
        <div className={styles.characteristicStory}>
          <h3>Story</h3>
          <h2>
            {product.brand} {product.model}
          </h2>
          <p>{product.story}</p>
        </div>
        <div className={styles.characteristicBenefits}>
          <h3>Benefits</h3>
          {Object.keys(product.advantages).map((key) => (
            <div key={key}>
              <h2>{key}</h2>
              <p>{product.advantages[key]}</p>
            </div>
          ))}
        </div>
      </div>
      <div className={styles.characteristicImage}>
        <img src={product.characteristic.path} />
      </div>
      <div className={styles.characteristicProduct}>
        <div className={styles.characteristicProductMini}>
          <div>
            <h2>{product.brand}</h2>
            <h3>{product.model}</h3>
          </div>
          <div className={styles.characteristicProductMiniColor}>
            {product.colors.map((item) => (
              <p>{item}/</p>
            ))}
          </div>
          <div className={styles.characteristicProductMiniImage}>
            <img src={product.img[0].path} />
          </div>
          <div className={styles.characteristicProductMiniPrice}>
            <p>En Stock</p>
            <p>$ {product.price}.00</p>
          </div>
          <div>
            <Formik
              initialValues={{ size: "size" }}
              onSubmit={(values, actions) => {
                addToCart(values);
              }}
            >
              {() => (
                <Form className={styles.characteristicProductMiniSize}>
                  <Field as="select" name="size">
                    <option value="SIZE">SIZE</option>
                    {product.size.map((item) => (
                      <option value={item}>{item}</option>
                    ))}
                  </Field>
                  <button type="submit">Add To Cart</button>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  cart: state.cart.cart,
  size: state.cart.size,
});

export default connect(mapStateToProps, { addProduct, addSelectedSize })(
  Characteristic
);
