import React, { useState } from "react";
import { connect } from "react-redux";
import logo1 from "../../../assets/img/products/5.png";
import styles from "./mainSectionProduct.module.scss";
import { addProduct, addSelectedSize } from "../../../store/reducers/cart";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { addToCartCurrentProduct } from "../../common/addToCartCurrentProduct/addToCartCurrentProduct";
import Carousel from "react-elastic-carousel";

const MainSectionProduct = ({ product, addProduct, addSelectedSize }) => {
  const addToCart = (values) => {
    addToCartCurrentProduct(values.size, product, addProduct, addSelectedSize);
  };
  console.log(product.img[0].path);
  return (
    <div className={styles.product}>
      <div className={styles.productImg}>
        <Carousel
          disableArrowsOnEnd={false}
          enableAutoPlay
          autoPlaySpeed={5000}
        >
          {product.img.map((item) => (
            <img key={item} src={item.path} />
          ))}
        </Carousel>
      </div>
      <div className={styles.productInfo}>
        <div className={styles.productFavorite}></div>
        <div className={styles.productText}>
          <h3 className={styles.productBrand}>{product.brand}</h3>
          <h2 className={styles.productTitle}>{product.model}</h2>
          <p className={styles.productPrice}>$ {product.price}.00</p>
          <div className={styles.productColor}>
            <p>
              <span>Colors - </span>
              {product.colors.map((item) => item + " / ")}
            </p>
            <div className={styles.productOtherItems}>
              <div className={styles.productOtherItem}>
                <img src={product.img[0].path} />
              </div>
              <div className={styles.productOtherItem}>
                <img src={product.img[0].path} />
              </div>
            </div>
          </div>
        </div>
        <div className={styles.productSize}>
          <p>Size charts</p>
          <Formik
            initialValues={{ size: "" }}
            onSubmit={(values, actions) => {
              addToCart(values);
            }}
          >
            {() => (
              <Form>
                <Field as="select" name="size">
                  {product.size.map((item) => (
                    <option value={item}>{item}</option>
                  ))}
                </Field>
                <button type="submit">Add To Cart</button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  cart: state.cart.cart,
});

export default connect(mapStateToProps, { addProduct, addSelectedSize })(
  MainSectionProduct
);
