const SET_FILTER_STATUS = "SET_FILTER_STATUS";
const SET_FILTER_SETTINGS = "SET_FILTER_SETTINGS";

let initialState = {
  isFiltered: false,
  category: [],
  size: [],
  colors: [],
  price: [],
  brands: [],
};

export const filter = (state = initialState, action) => {
  switch (action.type) {
    case SET_FILTER_STATUS: {
      return {
        ...state,
        isFiltered: action.status,
      };
    }
    case SET_FILTER_SETTINGS: {
      return {
        ...state,
        [action.typeFilter]: [...state[action.typeFilter], action.filter],
      };
    }
    default: {
      return {
        ...state,
      };
    }
  }
};

//action creator
export const setFilterStatus = (status) => {
  return {
    type: SET_FILTER_STATUS,
    status,
  };
};
export const setFilterSettings = (typeFilter, filter) => {
  return {
    type: SET_FILTER_SETTINGS,
    filter,
    typeFilter,
  };
};
